.header {
    background-color: rgb(255, 182, 182);
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 120px;
    grid-column: span 3;

    .logo {
        width: 80px;
        height: 80px;

        img {
            border-radius: 50%;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    nav {
        width: 20%;
        display: flex;
        justify-content: space-between;
    }
}